@import url(https://fonts.googleapis.com/css2?family=Roboto+Condensed&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  width: 90%;
  margin: 0 auto;
  padding: 15px 0;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

a {
  text-decoration: none;
  color: black;
}
.italic {
  font-style: italic;
}
.light-gray {
  color: rgb(110, 110, 110);
}
.App {
  justify-content: space-between;
  font-family: 'Roboto Condensed', sans-serif;
}
.header {
  width: 95%;
  margin: 0 auto;
  padding: 20px 0;
}
.first__container {
  display: flex;
  padding-bottom: 50px;
}
.third__container, .third__left, .third__right {
  display: flex;
}
.third__right {
  padding: 0 150px;
}
.third__left {
  padding-right: 150px;
}
.third__left div, .third__right div {
  margin-right: 20px;
}
.third__container {
  padding: 50px;
}

/* pfp */
.pfp__img {
  border-radius: 200px;
  width: 250px;
}
.pfp__detail {
  padding: 15px 20px;
}
.skills__pfp {
  display: flex;
}
.skills__pfp div div{
  background-color: rgb(233, 232, 232);
  margin: 5px;
  border-radius: 20px;
  padding: 5px 10px;
}
.skills__pfp-up {
  display: flex;
}
.skills__pfp-down {
  display: flex;
}
.name {
  font-weight: 700;
  font-size: 30px;
}
.link__projects {
  font-weight: 700;
}
.socialMedias__content{
 padding-right: 10px; 
}
.pfp__content {
  margin: 12px 0;
}
@media (max-width: 1024px) {
    .third__container {
        display: block;
    }
    .third__left {
        justify-content: center;
        padding: 0;
    }
    .third__right {
        justify-content: center;
    }
    .third__left div, .third__right div {
        padding: 0;
        margin: 5px;
    }

    /* projects */
    .list__project {
        width: 50%;
    }
}

@media (max-width: 775px) {
    .skills__pfp div {
        height: 20px;
    }
    .skills__fullstack {
        width: 80px;
    }
    .first__container {
        display: block;
    }
    .pfp__img__container {
        margin: 0 auto;
        width: -webkit-fit-content;
        width: -moz-fit-content;
        width: fit-content;
    }

    .first__container, .second__container {
        text-align: center;
    }
    .skills__pfp {
        width: -webkit-fit-content;
        width: -moz-fit-content;
        width: fit-content;
        margin: 0 auto;
        margin-bottom: 40px;
    }

    /* projects */
}

/* @media (max-width: 670px) {

} */

@media (max-width: 490px) {
    .skills__pfp {
        display: block;
    }
    .skills__pfp-up {
        margin-bottom: 20px;
    }
    .skills__pfp-up, .skills__pfp-down {
        justify-content: center;
    }
    .third__right {
        width: 100%;
        padding: 0;
    }
    .pfp__img, .skills__pfp, .third__left, .third__right {
        width: 100%;
    }
    
    .navbar__content {
        justify-content: start;
        margin: 0;
        padding: 0;
    }
    .link_right_content {
        margin-right: 10px;
    }

    /* projects */
    
}

@media (min-width: 385px) {
    .link_right_content {
        margin-right: 15px;
    }

    .logo {
        padding: 0 10px;
        margin-left: 30px;
        font-size: medium;
    }

    /* projects */
}
.Navbar {
    background-color: #FFF;
    height: 50px;
    font-size: small;
    margin-top: 5px;
    z-index: 10;
}
.no-deco {
    text-decoration: none;
    /* padding-right: 20px; */
    color: black;
    transition: 0.2s;
}
.no-deco:hover {
    color: rgb(2, 139, 202);
}

.logo {
    font-weight: 700;
}
.about {
    font-weight: 500;
}
.navbar__content {
    display: flex;
}
.left__content {
    padding-right: 30px;
}
.right__content {
    padding-top: 2px;
}

