    flex-wrap: wrap;
}
*/

.list__project {
    width: 30%;
    padding-bottom: 20px;
    margin: 5px;
}

.project__content {
    margin: 0 auto;
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;

}

@media (max-width: 1024px) {
    .list__projects {
        margin: 0 auto;
    }
    .list__project {
        width: 50%;
        margin: 0 auto;
    }
}

@media (max-width: 815px) {
    .list__project {
        width: 100%;
    }
}
